import ky from 'ky';
import { ExecuteResult } from '@/components/pages/InjectionExecute/executeResult';
import { InjectionExecutePatch } from '@/components/pages/InjectionExecute/injectionExecutePatch';

const doExecution = async (
  execute: InjectionExecutePatch,
): Promise<boolean> => {
  try {
    await ky
      .post(`http://localhost:8080/jerseywebapp/rest/order/injection`, {
        json: execute,
        timeout: false,
      })
      .json<ExecuteResult>();

    return true;
  } catch (err) {
    // eslint-disable-next-line no-alert
    alert(err);

    return false;
  }
};

export default doExecution;
